import React,{Component} from 'react';
import './index.less';
export default class Star extends Component{
    constructor(props){
        super(props);
        let arr = [];
        for(let i = 1;i<=5;i++){
            if(i<=props.count){
                arr.push(true);
            }else{
                arr.push(false);
            }
        }
        this.state ={
            arr
        }
    }
    render(){
        return (
            <div>
                {
                    this.state.arr.map((item,index)=>(
                            item?<i key={index} className="iconfont font icon-collection_fill"></i>:<i className="iconfont icon-collection" key={index}></i>
                    ))
                }
            </div>
        )
    }
}


